.checkoutContainer{
  display: flex;
    width: 100%;
    height: 100%;
}

.clientData {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .submitbtn {
    width: 100%;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .submitbtn:hover {
    background-color: #45a049;
  }
  
  .checkout {
    flex: 1;
    border-radius: 5px;
    padding: 20px;
    box-sizing: border-box;
  }

  .cartItems {
    flex: 1;
    padding: 20px;
    overflow-y: auto; /* Add scroll if content overflows */
  }
  
  .cartItem {
    display: flex;
    margin-bottom: 20px;
    border-radius: 10px;
    box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.1);
  }
  
  .img-container {
    flex: 1;
    padding: 10px;
  }
  
  .img-container img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }
  
  .item-details {
    flex: 2;
    padding: 10px;
  }
  
  .item_title {
    font-weight: bold;
  }
  
  .text {
    display: flex;
    justify-content: space-between;
    width: 130px;
    align-items: center;
    margin-top: 5px;
  }
  
  .highlight {
    color: #e74c3c;
  }
  .cartSummary {
    margin-top: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
  }
  
  .summaryText {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .summaryText span {
    font-size: 16px;
  }
  
  .highlight2 {
    color: #e74c3c;
    font-weight: bold;
  }

  @media screen and (max-width: 700px) {
    .checkoutContainer {
      display: block;
    }
  }
  .form-control {
  position: relative;
  margin: 20px 0 40px;
  width: 100%;
}

.form-control input {
  background-color: transparent;
  border: 0;
  border-bottom: 1px #323030 solid;
  display: block;
  width: 100%;
  padding: 15px 0;
  font-size: 18px;
  color: #151515;
}

.form-control input:focus,
.form-control input:valid {
  outline: 0;
  border-bottom-color:rgb(185, 180, 180);
}

.form-control label {
  position: absolute;
  top: 15px;
  left: 0;
  pointer-events: none;
}

.form-control label span {
  display: inline-block;
  font-size: 18px;
  min-width: 5px;
  color: #484848;
  transition: 0.3s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.form-control input:focus+label span,
.form-control input:not(:placeholder-shown) + label span {
  color: rgb(137, 135, 135);
  transform: translateY(-30px);
}
  
