@import "../../css-config/mixins.scss";
.footer {
    width: 100%;
    .footer-content {
        padding: 50px 20px;
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        flex-flow: wrap;
        gap: 20px;
        @include md {
            justify-content: space-between;
            padding: 50px 0;
        }
        .col {
            max-width: 300px;
            @include md {
                max-width: 300px;
            }
            &:nth-child(3) {
                width: 150px;
                @include md {
                    width: auto;
                }
            }
            .title {
                margin-bottom: 20px;
                font-size: 20px;
            }
            .text {
                color: rgba(0, 0, 0, 0.5);
                font-size: 14px;
            }
            .c-item {
                display: flex;
                margin-bottom: 10px;
                svg {
                    flex-shrink: 0;
                    font-size: 14px;
                    margin-right: 10px;
                    margin-top: 4px;
                    color: rgba(0, 0, 0, 0.5);
                }
            }
            span {
                &.text {
                    display: block;
                    margin-bottom: 10px;
                    cursor: pointer;
                }
            }
            a {
                text-decoration: none;
            }
        }
    }
    .bottom-bar {
        border-top: 1px solid rgba(0, 0, 0, 0.1);
    
        .bottom-bar-content {
            padding: 20px;
            display: flex;
            align-items: center;
            flex-direction: column;
            text-align: center;
    
            @include md {
                padding: 0;
                height: 60px;
                max-width: 1200px;
                margin: 0 auto;
                flex-direction: row;
                justify-content: space-between;
                text-align: left;
            }
    
            .text {
                font-size: 15px;
                flex: 1;
                color: rgba(0, 0, 0, 0.5);
            }
    
            .text2 {
                font-size: 15px;
                color: rgba(0, 0, 0, 0.5);
    
                a {
                    text-decoration: none;
                    color: black;
                }
            }
        }
    
        @media screen and (max-width: 500px) {
            .bottom-bar-content {
                padding: 10px;  // Adjust the padding or any other styles for small screens
                text-align: center;
                .text, .text2 {
                    margin: 5px auto;
                    justify-content: center;
                    font-size: 13px;
                    width: 100%;
                }
            
            }
        }
    }
    
}

