@import "../../css-config/mixins.scss";
.single-product-main-content {
    margin: 20px 0;
    width: 90%;
    @include md {
        margin: 75px 0;
    }
    .layout {
        width: 100%;
        max-width: calc(100% - 20px);
        margin: 0 auto;
        @include md {
            max-width: 1200px;
        }
        .single-product-page {
            width: 100%;
            display: flex;
            flex-direction: column;
            @include lg {
                flex-direction: row;
            }
            .left {
                width: 100%;
                flex-shrink: 0;
                @include lg {
                  width: 650px; /* Adjust as needed for medium screens */
                  height: 100%;
                }
              
                /* Ensure the slider within this container remains responsive */
                .slider-container {
                  width: 100%; /* Force full-width on all screens */
                  @include md {
                    width: auto; /* Reset width for medium screens if needed */
                  }
                }
              
                /* Override styles for slider images */
                .slider-container .slider-image {
                  max-width: 100%; /* Limit image width to the container width */
                  height: auto; /* Auto adjust height */
                }
                /* Styles for the slider dots */
              
  
              }
            .right {
                display: flex;
                flex-direction: column;
                padding-top: 50px;
                @include md {
                    padding: 40px 30px;
                }
                .title {
                    font-size: 25px;
                    line-height: 28px;
                    margin-bottom: 30px;
                    letter-spacing: 3px;
                    @include md {
                        font-size: 30px;
                        line-height: 32px;
                    }
                }
                .price {
                    font-size: 25px;
                    line-height: 32px;
                    margin-bottom: 30px;
                }
                .old-price-label {
                    margin-left: 10px;
                    text-decoration: line-through;
                    font-size: 24px;
                    color:#473c3c;
                }
                .desc {
                    font-size: 15px;
                    line-height: 23px;
                    margin-bottom: 30px;
                    color: #6b6b6b;
                    white-space: pre-line;
                    letter-spacing: 1px;
                    @include md {
                        font-size: 17px;
                        line-height: 27px;
                        margin-bottom: 20px;
                    }
                }
                .product_size {
                    display: flex;
                    
                    margin: 30px 0;
                   
                }
                .psize {
                    width: 70px;
                    height: 35px;
                    border: 1px solid #000;
                    color: #000;
                    text-align: center;
                    padding-top: 5px;
                    margin-right: 20px;
                    cursor: pointer;
                }
                .psize.active {
                    border-width: 1.5px;
                    background-color:lightslategray;
                    color: #fff;
                }
                .cart-buttons {
                    display: flex;
                    @include md {
                        margin-top: 30px;
                    }
                    .quantity-buttons {
                        width: fit-content;
                        display: flex;
                        border: 2px solid rgba(0, 0, 0, 0.2);
                        margin-right: 10px;
                        height: 50px;
                        span {
                            font-size: 18px;
                            width: 40px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            cursor: pointer;
                            color: #6b6b6b;
                            &:nth-child(1) {
                                border-right: 2px solid rgba(0, 0, 0, 0.2);
                            }
                            &:nth-child(2) {
                                width: 60px;
                            }
                            &:nth-child(3) {
                                border-left: 2px solid rgba(0, 0, 0, 0.2);
                            }
                        }
                    }
                    .add-to-cart-button {
                        text-decoration: none;
                        outline: 0;
                        border: 0;
                        height: 50px;
                        width: 180px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                        font-size: 16px;
                        color: white;
                        background-color: lightslategray;
                        flex-grow: 1;
                        @include md {
                            flex-grow: unset;
                        }
                        svg {
                            margin-right: 10px;
                        }
                    }
                }

                .divider {
                    margin: 30px 0;
                    height: 1px;
                    width: 100%;
                    background-color: rgba(0, 0, 0, 0.1);
                }

                .info-item {
                    .text-bold {
                        font-size: 18px;
                        font-weight: 500;
                        display: block;
                        &:nth-child(1) {
                            margin-bottom: 20px;
                        }
                        span {
                            font-size: 16px;
                            font-weight: 400;
                            cursor: pointer;
                            color: #6b6b6b;
                        }
                        svg {
                            margin: 0 5px;
                        }
                    }
                }
            }
        }
    }
}


