@import "../../../css-config/mixins.scss";
.product-card {
    cursor: pointer;
    position: relative;
    overflow: hidden;
    width: calc(50% - 5px);
    border-radius: 10px;
    @include md {
        width: calc(25% - 25px);
    }
    padding: 4px;
    .thumbnail {
        width: 100%;
        height: 180px;
        background-color:#fff;
        padding: 2px;
        display: flex;
        align-items: center;
        border-top-left-radius: 7px; 
        border-top-right-radius: 7px;
        @include md {
            height: 400px;
        }
        img {
            transition: all ease 0.3s;
            display: block;
            width: 100%;
        }
    }
    .prod-details {
        background-color:#fff;
        border-bottom-left-radius: 7px; 
        border-bottom-right-radius: 7px;
        .name {
            font-size: 14px;
            display: block;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            
            @include md {
                font-size: 16px;
                margin-bottom: 10px;
            }

        }
        .price {
            font-size: 15px;
            @include md {
                font-size: 20px;
            }
        }
        .sold-out-label {
            color: #000;
            padding: 3px 7px; 
            border: #000 solid 1px;
            border-radius: 3px; 
            font-size: 15px;
            
            width: 90px;
        }
        .old-price-label {
            margin-left: 10px;
            text-decoration: line-through;
            font-size: 12px;
            color:#473c3c;
            @include md {
                font-size: 20px;
            }
        }
    }
    &:hover {
        .thumbnail {
            img {
                transform: scale(1.2);
            }
        }
    }
    .prenium {
        position: absolute;
        overflow: hidden;
        width: 100px;
        height: 100px;
        top: -10px;
        left: -10px;
        display: flex;
        align-items: center;
        justify-content: center;
        @include md {
            width: 200px;
            height: 200px;
        }
      }
      
    .prenium::before {
        content: 'sold out';
        position: absolute;
        width: 140%;
        height: 20px;
        background-image: linear-gradient(45deg, #2f7858 0%, #50e3b7  51%, #2f7858  100%);
        transform: rotate(-45deg) translateY(-20px);
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        font-weight: 600;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        font-size: 9px;
        box-shadow: 0 5px 10px rgba(0,0,0,0.23);
        @include md {
            width: 150%;
            height: 40px;
            font-size: 20px;
        }
      }
      
    .prenium::after {
        content: '';
        position: absolute;
        width: 10px;
        bottom: 0;
        left: 0;
        height: 10px;
        z-index: -1;
        box-shadow: 140px -140px #cc3f47;
        background-image: linear-gradient(45deg, #FF512F 0%, #F09819  51%, #FF512F  100%);
      }
    .blob {
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        width: 150px;
        height: 150px;
        border-radius: 50%;
        background-color: #ff0000a2;
        opacity: 1;
        filter: blur(12px);
        animation: blob-bounce 5s infinite ease;
        @include md {
            width: 250px;
            height: 250px;
        }
    }
    @keyframes blob-bounce {
        0% {
          transform: translate(-100%, -100%) translate3d(0, 0, 0);
        }
      
        25% {
          transform: translate(-100%, -100%) translate3d(100%, 0, 0);
        }
      
        50% {
          transform: translate(-100%, -100%) translate3d(100%, 100%, 0);
        }
      
        75% {
          transform: translate(-100%, -100%) translate3d(0, 100%, 0);
        }
      
        100% {
          transform: translate(-100%, -100%) translate3d(0, 0, 0);
        }
      }
      
    
}

  
  
  
  
