@import "../../../css-config/mixins.scss";
@import url('https://fonts.googleapis.com/css?family=Anton');
@import url('https://fonts.googleapis.com/css?family=Poppins');

.hero-banner {
    position: relative;
    width: 100%;
    height: 90vh;
    background-image: url("../../../assets/banner5.jpg");
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
  }
  .content {
    max-width: 800px;
    padding: 0 20px;
    margin-top: 250px;
  }

  .text-content {
    align-items: center;
    justify-content: center;
  }

  .text-content h1 {
    font-size: 38px;
    font-family:Arial, Helvetica, sans-serif;
    font-weight: 900;
    color: white;
    margin-bottom: 290px;
    
    
  }
  
  .text-content p {
    display: none;
  }
  .ctas {
    justify-content: center;
    
    align-items: center;
    .banner-cta {
        justify-content: center;
        align-items: center;
        margin: auto;
        text-transform: uppercase;
        font-size: 13px;
        font-weight: 500;
        width: fit-content;
        border: 2px solid white;
        padding: 10px 20px;
        transition: all ease 0.3s;
        cursor: pointer;
        &.v2 {
            background-color: white;
            color: black;
        }
        &:hover {
            opacity: 0.6;
        }
    }
}

  .banner-cta:hover {
    background-color: #f0f0f0;
  }

  @media only screen and (max-width: 800px) {
    .hero-banner {
      background-image: url("../../../assets/banner5b.png");
      height: 95vh;
    }
    .text-content h1 {
      font-size: 25px;
      margin-left: 25px;
      margin-bottom: 260px;
    }
    
  }

  @media only screen and (max-width: 600px) {
    .hero-banner {
      background-image: url("../../../assets/banner5a.png");
      height: 93vh;
    }
    .content {
      margin-top: 200px;
    }
    .text-content h1 {
      font-size: 18px;
      margin-bottom: 160px;
    }
    .text-content p {
      font-family:'Poppins';
      display: block;
      font-size: 12px;
      line-height: 1.5;
      text-align: center;
      margin-bottom: 50px;
    }
   
  }

