@import "../../css-config/mixins.scss";
.cart-panel {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    display: flex;
    justify-content: flex-end;

    .opac-layer {
        background: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .cart-content {
        width: 100%;
        height: 100%;
        background: white;
        position: relative;
        z-index: 1;
        display: flex;
        flex-direction: column;
        transform: translateX(100%);
        animation: slideCartWindow 0.3s ease forwards;
        @include md {
            width: 340px;
        }
        .cart-header {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding: 20px 15px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
            .heading {
                flex-grow: 1;
                margin-bottom: 0;
                font-size: 20px;
                font-weight: 700;
                text-transform: uppercase;
            }
            .close-btn {
                display: flex;
                align-items: center;
                gap: 5px;
                cursor: pointer;
                svg {
                    font-size: 18px;
                }
                .text {
                    text-transform: uppercase;
                    font-size: 14px;
                }
                &:hover {
                    opacity: 0.5;
                }
            }
        }

        .empty-cart {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            margin-top: 100px;
            svg {
                font-size: 120px;
                opacity: 0.1;
            }
            span {
            }
            .return-cta {
                outline: 0;
                border: 0;
                height: 40px;
                width: 150px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                font-size: 13px;
                color: white;
                background: lightslategray;
                border-bottom: 3px solid lightslategray;
                svg {
                    margin-right: 10px;
                }
            }
        }

        .cart-footer {
            border-top: 1px solid rgba(0, 0, 0, 0.1);
            .subtotal {
                padding: 20px 15px;
                border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                display: flex;
                justify-content: space-between;
                .text {
                    margin-bottom: 0;
                    font-size: 20px;
                    font-weight: 700;
                    text-transform: uppercase;
                    &.total {
                        color: lightslategray;
                    }
                }
            }
            .cart_button {
                padding-top: 20px;
                padding-left: 15px;
                padding-right: 15px;
                .checkout-cta {
                    text-decoration: none;
                    outline: 0;
                    border: 0;
                    height: 50px;
                    width: 100%;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    font-size: 16px;
                    color: white;
                    background: lightslategray;
                    text-transform: uppercase;
                    svg {
                        margin-right: 10px;
                    }
                }
            }
        }
    }
}

.continue-cta {
    text-decoration: none;
    height: 50px;
    width: 100%;  
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 16px;
    color: #000;
    background-color: #fff;
    text-transform: uppercase;
    @include md {
        display: none;
    }
}

.cart-items-scroll {
    width: 100%;
    max-height: 290px; /* Adjust as needed */
    overflow-y: auto;
    @include md {
        max-height: 480px;
    }
}

@keyframes slideCartWindow {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(0);
    }
}
.promo-code {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold; /* Add this line to emphasize the promo code section */
}

/* Style for the promo code input field */
.promo-code-input {
    padding: 8px;
    border: 1px solid #ccc;
    margin-right: 10px;
    font-size: 16px;
}

/* Style for the "Apply" button */
.apply-promo-btn {
    background-color:lightslategray;
    color: #fff;
    padding: 8px 16px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
}

.apply-promo-btn:hover {
    background-color:gray;
}

.promo-applied-msg {
    padding: 5px 10px; /* Padding around the message */ /* Rounded corners */
    font-size: 15px; /* Font size */
    display: inline-block; /* Display as inline-block so it doesn't take full width */
    margin-top: 10px; /* Add some space at the top of the message */
}